import { Command, program } from "commander"
import _fs from "fs"
import _path from "path"
import { argv } from "process"
import { version } from "../core/Version"
import { CliDesc, addFooter } from "../core/cli/CliConstants"
import { verifyUidGid } from "../core/cli/VerifyUidGid"

program.version(version)

program.description(
  `Welcome to PhotoStructure, your new home for all your photos and videos.\nSee https://photostructure.com/server/tools/ for details about these tools.`
)

function prefix(cmd: string) {
  for (const dir of ["bin", __dirname]) {
    const path = _path.join(dir, cmd)
    if (_fs.existsSync(path)) return path
  }
  console.error("failed to find command " + cmd)
  return cmd
}

verifyUidGid()

addFooter(program as Command)
  .command("main", CliDesc.main + " (default)", {
    isDefault: true,
    executableFile: prefix("main.js")
  })
  .command("info", CliDesc.info, {
    executableFile: prefix("info.js")
  })
  .command("list", CliDesc.list, {
    executableFile: prefix("list.js")
  })
  .command("logcat", CliDesc.logcat, {
    executableFile: prefix("logcat.js")
  })
  .command("logtail", CliDesc.logtail, {
    executableFile: prefix("logtail.js")
  })
  .command("web", CliDesc.web, {
    executableFile: prefix("web.js")
  })
  .command("sync", CliDesc.sync, {
    executableFile: prefix("sync.js")
  })
  .parse(argv)
